.btn {
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.1em solid #ffffff;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.btn:hover {
  color: #000000;
  background-color: #ffffff;
}
@media all and (max-width: 30em) {
  .btn {
    display: block;
    margin: 0.4em auto;
  }
}

.vertex{opacity:0.75;width:25px;height:25px;outline:1px solid rgba(255,255,255,0.699);background:inherit;display:inline-block;background:#1c568d}.vertex-non-draggable{user-select:none;-moz-user-select:none;-webkit-user-drag:none;-webkit-user-select:none;-ms-user-select:none}.vertex:hover{outline:1px solid #2c2c2c;display:inline-block}.vertex-finish{background-color:red}.vertex-finish:hover{background-color:red;cursor:-webkit-grabbing;cursor:grabbing}.vertex-start{background-color:#ffef47}.vertex-start:hover{background-color:#ff8266;cursor:-webkit-grabbing;cursor:grabbing}.vertex-wall{background-color:#071421;-webkit-animation-name:opacityOn;animation-name:opacityOn;-webkit-animation-duration:0.2s;animation-duration:0.2s;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-delay:0s;animation-delay:0s;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards;-webkit-animation-play-state:running;animation-play-state:running}.vertex-wall:hover{background-color:#0c3547}.vertex-visited{-webkit-animation-name:visitedAnimation;animation-name:visitedAnimation;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-delay:0;animation-delay:0;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:running;animation-play-state:running}.vertex-shortest-path{-webkit-animation-name:shortestPath;animation-name:shortestPath;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation-delay:0;animation-delay:0;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:running;animation-play-state:running}@-webkit-keyframes opacityOn{0%{-webkit-transform:scale(0.95);transform:scale(0.95);background-color:#333}50%{-webkit-transform:scale(1.05);transform:scale(1.05);background-color:#333}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#333}}@keyframes opacityOn{0%{-webkit-transform:scale(0.95);transform:scale(0.95);background-color:#333}50%{-webkit-transform:scale(1.05);transform:scale(1.05);background-color:#333}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#333}}@-webkit-keyframes visitedAnimation{0%{-webkit-transform:scale(0.3);transform:scale(0.3);background-color:rgba(0,0,66,0.75);border-radius:100%}50%{background-color:rgba(17,104,217,0.75)}75%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:rgba(0,217,159,0.75)}100%{-webkit-transform:scale(1);transform:scale(1);background-color:rgba(0,190,218,0.75)}}@keyframes visitedAnimation{0%{-webkit-transform:scale(0.3);transform:scale(0.3);background-color:rgba(0,0,66,0.75);border-radius:100%}50%{background-color:rgba(17,104,217,0.75)}75%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:rgba(0,217,159,0.75)}100%{-webkit-transform:scale(1);transform:scale(1);background-color:rgba(0,190,218,0.75)}}@-webkit-keyframes shortestPath{0%{-webkit-transform:scale(0.6);transform:scale(0.6);background-color:#ffef47}50%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#ffef47}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#ffef47}}@keyframes shortestPath{0%{-webkit-transform:scale(0.6);transform:scale(0.6);background-color:#ffef47}50%{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#ffef47}100%{-webkit-transform:scale(1);transform:scale(1);background-color:#ffef47}}

.container{display:flex;flex-direction:row;align-items:center;justify-content:space-around;width:100%;height:50px;background-color:#12375a;z-index:0;top:0px;position:absolute;color:#87edff;padding-top:30px;padding-bottom:30px;box-shadow:0px 0px 16px 0px rgba(0,0,0,0.28)}.main-nav{display:flex;flex-direction:row;justify-content:space-around}.buttons{display:flex;flex-direction:row;align-items:center}.btn{padding:10px 10px;width:100px;background-color:#ffef47;margin:auto 20px;color:#12375a;font-family:"Ubuntu",sans-serif}.algo-methods{display:block;margin-top:130px;text-align:center;color:#87edff;font-family:"Ubuntu",sans-serif}.underlined-a{text-decoration:none;color:aqua;padding-bottom:0.15em;box-sizing:border-box;box-shadow:inset 0 -0.2em 0 aqua;-webkit-transition:0.2s;transition:0.2s}.underlined-a:hover{color:#222;box-shadow:inset 0 -2em 0 aqua;-webkit-transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1);transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1)}.brk-btn{padding:10px 10px;width:100px;margin:auto 20px;font-family:"Ubuntu",sans-serif;position:relative;background:none;color:#ffef47;text-transform:uppercase;text-decoration:none;border:0.2em solid #ffef47}.brk-btn::before{content:"";display:block;position:absolute;width:10%;background:#12375a;height:0.3em;right:20%;top:-0.21em;-webkit-transform:skewX(-45deg);transform:skewX(-45deg);-webkit-transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1);transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1)}.brk-btn::after{content:"";display:block;position:absolute;width:10%;background:#12375a;height:0.3em;left:20%;bottom:-0.25em;-webkit-transform:skewX(45deg);transform:skewX(45deg);-webkit-transition:all 0.45 cubic-bezier(0.86, 0, 0.07, 1);-webkit-transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1);transition:all 0.45s cubic-bezier(0.86, 0, 0.07, 1)}.brk-btn:hover::before{right:80%}.brk-btn:hover::after{left:80%}

